li,
ul,
ol {
  list-style: none;
  padding: 0;
}

.nav {
  width: 100%;
  height: 80px;
  position: absolute;
  z-index: 2;

  &__list {
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 5px 100px 0 0;
  }

  &__link {
    margin-right: 25px;
  }

  a,
  button {
    font-weight: 400;
    font-size: 16px;
    text-decoration: none;
    color: white;
  }

  .active {
    font-weight: 700;
    line-height: 140%;
    text-align: center;
    background-color: #b59571;
    color: white;
    text-decoration: none;
    padding: 15px 30px;
    border: 0;
    min-width: 150px;
    cursor: pointer;
  }
}

@media (max-width: 1000px) {
  .nav__list {
    margin: 5px 30px 0 0;
  }
}

@media (max-width: 600px) {
  .nav {
    &__list {
      margin: 5px 0;
    }
    a,
    button {
      font-size: 14px;
    }

    .active {
      padding: 15px 25px;
    }
  }
}

@media (max-width: 470px) {
  .nav {
    &__list {
      margin: 5px 0;
    }
    &__link {
      margin-right: 15px;
    }
    a,
    button {
      font-size: 14px;
    }

    .active {
      padding: 15px 15px;
    }
  }
}

@media (max-width: 400px) {
  .nav {
    display: none;
  }
}